/* CONTAINER */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
  max-width: 320px;
}
@media (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 20px;
  }
}
@media (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding: 0 40px;
  }
}

/* BLOCKS */
.button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  background-color: #7859cf;
  font-size: 14px;
  line-height: 24px;
  color: #ffffff;
  font-family: "Montserrat", "Tahoma", sans-serif;
  border-radius: 4px;
  cursor: pointer;
  width: 141px;
  height: 36px;
}
.button--order {
  text-decoration: none;
  width: 113px;
  height: 40px;
}
.button--order:hover, .button--order:focus {
  background-color: #9070ec;
}
.button--order:active {
  background-color: #593cac;
}

.header {
  position: relative;
  min-height: 60px;
  background-color: #ffffff;
}

.header__nav {
  display: none;
  position: absolute;
  top: 61px;
  left: 0;
  background-color: #ffffff;
  width: 100%;
  z-index: 3;
}
.header__nav--open {
  display: block;
}
@media (min-width: 768px) {
  .header__nav {
    display: block;
    position: static;
    width: 440px;
  }
}

.header__logo {
  margin: 0;
  display: flex;
  align-items: center;
}
@media (min-width: 1440px) {
  .header__logo {
    column-gap: 20px;
  }
}

.header__promo-text {
  margin: 0;
  display: none;
}
@media (min-width: 1440px) {
  .header__promo-text {
    display: inline-block;
    font-size: 12px;
    left: 16px;
    width: 126px;
  }
}

@media (min-width: 768px) {
  .header__logo-img {
    width: 82px;
  }
}
@media (min-width: 1440px) {
  .header__logo-img {
    width: 170px;
  }
}

.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding-right: 0;
}
@media (min-width: 768px) {
  .header__wrapper {
    padding-right: 20px;
  }
}
@media (min-width: 1440px) {
  .header__wrapper {
    padding-right: 40px;
  }
}

.header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
}
@media (min-width: 768px) {
  .header__nav-list {
    display: flex;
    flex-wrap: wrap;
    min-height: 80px;
    align-items: center;
    justify-content: center;
  }
}
.header__nav-list .header__nav-item {
  position: relative;
  border: 1px solid #eae6fc;
  padding: 20px 50px;
}
@media (min-width: 768px) {
  .header__nav-list .header__nav-item {
    border: none;
    padding: 30px 20px;
  }
  .header__nav-list .header__nav-item--active::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    background-color: #7859cf;
  }
}

.header__nav-link {
  text-decoration: none;
  color: #333333;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
}
.header__nav-link:hover {
  color: #7859cf;
}

.header__users {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.header__users-link {
  width: 60px;
  height: 60px;
  padding: 22px;
  text-decoration: none;
}
@media (min-width: 768px) {
  .header__users-link {
    padding: 32px 22px;
    height: 80px;
  }
}
@media (min-width: 1440px) {
  .header__users-link {
    display: flex;
    align-items: center;
    width: 129px;
  }
  .header__users-link--user {
    width: 112px;
  }
}
.header__users-link svg {
  fill: #333333;
}
.header__users-link:hover {
  background-color: #f0ebff;
}
.header__users-link:hover > svg {
  fill: #7859cf;
}
.header__users-link:hover > .header__users-text {
  color: #7859cf;
}
.header__users-link .header__users-text,
.header__users-link .header__users-cart-text {
  display: none;
}
@media (min-width: 1440px) {
  .header__users-link .header__users-text,
.header__users-link .header__users-cart-text {
    display: inline-block;
    margin-left: 5px;
    text-transform: uppercase;
    font-size: 12px;
    line-height: 16px;
    color: #333333;
  }
}

.header__users-button {
  position: relative;
  display: block;
  background-color: transparent;
  width: 60px;
  height: 60px;
  border: none;
  cursor: pointer;
  padding: 0;
}
.header__users-button:hover, .header__users-button:focus {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .header__users-button {
    display: none;
  }
}
.header__users-button::after {
  content: "";
  position: absolute;
  transform: translate(-50%, -50%);
  width: 16px;
  height: 16px;
  background-image: url("../img/burger.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.header__users-button--open::after {
  background-image: url("../img/burger-open.svg");
}

.product {
  background-color: #f3ebe1;
}
@media (min-width: 1440px) {
  .product {
    background: #f3ebe1;
  }
}

.product__wrapper {
  position: relative;
}

.product__item {
  display: grid;
  min-height: 670px;
  justify-items: center;
  background-color: #f3ebe1;
  padding: 20px 20px 60px;
}
.product__item--two {
  background-color: #eae6fc;
}
.product__item--three {
  background-color: #e5e6e8;
}
@media (min-width: 768px) {
  .product__item {
    padding-top: 60px;
    min-height: 828px;
    background: linear-gradient(180deg, #f3ebe1 70%, #ffffff 70%);
  }
  .product__item--two {
    background: linear-gradient(180deg, #eae6fc 70%, #ffffff 70%);
  }
  .product__item--three {
    background: linear-gradient(180deg, #e5e6e8 70%, #ffffff 70%);
  }
}
@media (min-width: 1440px) {
  .product__item {
    min-height: 714px;
    padding-top: 0;
    display: flex;
    column-gap: 30px;
    align-items: center;
    background: linear-gradient(180deg, #f3ebe1 100%, #ffffff 0%);
  }
  .product__item--two {
    background: linear-gradient(180deg, #eae6fc 100%, #ffffff 0%);
  }
  .product__item--three {
    background: linear-gradient(180deg, #e5e6e8 100%, #ffffff 0%);
  }
}

@media (min-width: 768px) {
  .product__picture {
    grid-row: 2;
    justify-self: center;
  }
}

.product__img {
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .product__img {
    width: 476px;
  }
}
@media (min-width: 1440px) {
  .product__img {
    width: 600px;
  }
}

.product__content {
  display: grid;
  justify-items: center;
}
@media (min-width: 768px) {
  .product__content {
    margin-bottom: 20px;
  }
}
@media (min-width: 1440px) {
  .product__content {
    grid-template-rows: repeat(4, min-content);
    justify-items: start;
  }
}

.product__new {
  font-weight: 700;
  display: inline-block;
  color: #7859cf;
  text-transform: uppercase;
  margin-bottom: 4px;
}
@media (min-width: 1440px) {
  .product__new {
    grid-column: 2/2;
  }
}

.product__title {
  line-height: 47px;
  font-size: 36px;
  font-weight: 700;
  text-align: center;
  margin: 0;
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .product__title {
    line-height: 80px;
    font-size: 56px;
  }
}
@media (min-width: 1440px) {
  .product__title {
    grid-column: 2/2;
  }
}

.product__text {
  margin: 0 auto;
  margin-bottom: 20px;
  text-align: center;
}
@media (min-width: 768px) {
  .product__text {
    line-height: 32px;
    font-size: 20px;
    width: 608px;
  }
}
@media (min-width: 1440px) {
  .product__text {
    grid-column: 2;
    text-align: start;
    margin-bottom: 40px;
  }
}

.product__order {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 0;
  column-gap: 20px;
}
.product__order del {
  color: #7859cf;
  font-size: 16px;
  line-height: 32px;
}
.product__order b {
  font-size: 20px;
  font-weight: 700;
  line-height: 32px;
}
@media (min-width: 1440px) {
  .product__order {
    grid-column: 2;
  }
}

.product__button {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  border: none;
  background-color: transparent;
  width: 20px;
  height: 100%;
  padding: 0;
  cursor: pointer;
  z-index: 50;
}
.product__button:hover, .product__button:focus {
  background: linear-gradient(90deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
}
.product__button:active {
  background: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
}
.product__button svg {
  fill: #7859cf;
}
.product__button--left {
  left: 0;
  top: 0;
}
.product__button--right {
  right: 0;
  top: 0;
  transform: rotate(180deg);
}
@media (min-width: 768px) {
  .product__button {
    width: 80px;
  }
}
@media (min-width: 1440px) {
  .product__button {
    height: 100%;
  }
}

.product__pagination {
  display: none;
}
.product__pagination .swiper-pagination-bullet {
  border: none;
  background-color: #ffffff;
  height: 4px;
  width: 62px;
  border-radius: 0;
}
.product__pagination .swiper-pagination-bullet:hover, .product__pagination .swiper-pagination-bullet:focus {
  background-color: rgba(120, 89, 207, 0.25);
}
@media (min-width: 1440px) {
  .product__pagination {
    display: block;
    margin-bottom: 60px;
  }
}
.product__pagination .swiper-pagination-bullet-active {
  background-color: #7859cf;
}

