  margin: 0;
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .form {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 22px;
    margin-bottom: 44px;
  }
}
@media (min-width: 1440px) {
  .form {
    grid-template-columns: 318px;
