.form__range {
  margin-bottom: 24px;
}

.form__text {
  display: inline-block;
  margin-bottom: 22px;
  font-weight: 600;
}
@media (min-width: 1440px) {
  .form__text {
    margin-bottom: 30px;
  }
}

.form__scale {
  position: relative;
  margin-bottom: 35px;
  width: 100%;
  height: 2px;
  background-color: #e2e2e2;
}
@media (min-width: 768px) {
  .form__scale {
    width: 220px;
  }
}
@media (min-width: 1440px) {
  .form__scale {
    width: 100%;
  }
}
.form__scale::after, .form__scale::before {
  position: absolute;
  border-radius: 50%;
  border: 2px solid #7859cf;
  content: "";
  width: 24px;
  height: 24px;
  background-color: #ffffff;
  transform: translate(-50%, -50%);
}
.form__scale--min::before {
  left: 10px;
}
.form__scale--max::after {
  right: 30px;
}
.form__scale .form__bar {
  display: block;
  background-color: #7859cf;
  height: 2px;
  width: 80%;
}

.form__count {
  position: relative;
  display: flex;
  justify-content: space-between;
}
.form__count::after {
  position: absolute;
  content: "";
  width: 8px;
  height: 2px;
  background-color: #bdbdbd;
  left: 50%;
  top: 18px;
}
.form__count input {
  text-align: center;
  width: 118px;
  padding: 6px 10px;
  border: 2px solid #7859cf;
  border-radius: 4px;
  outline: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .form__count input {
    width: 92px;
  }
}

.form__buttons {
  text-align: center;
}
@media (min-width: 768px) {
  .form__buttons {
    display: flex;
    flex-wrap: wrap;
    column-gap: 30px;
    row-gap: 10px;
    grid-column: 1/-1;
    justify-content: center;
    margin-bottom: 30px;
  }
}
@media (min-width: 1440px) {
  .form__buttons {
    flex-wrap: nowrap;
  }
}

.form__button {
  border: none;
  font-size: 14px;
  line-height: 24px;
  color: #ffffff;
  font-family: "Montserrat", "Tahoma", sans-serif;
  border-radius: 4px;
  cursor: pointer;
  width: 160px;
  height: 45px;
}
.form__button--submit {
  background-color: #7859cf;
  margin-bottom: 24px;
}
.form__button--submit:hover, .form__button--submit:focus {
  background-color: #9070ec;
}
.form__button--submit:active {
  background-color: #7859cf;
}
@media (min-width: 768px) {
