    margin-bottom: 0;
  }
}
.form__button--reset {
  background-color: #ffffff;
  color: #7859cf;
}
.form__button--reset:hover, .form__button--reset:focus {
  background-color: #9070ec;
  color: #ffffff;
}
.form__button--reset:active {
  background-color: #7859cf;
}

.choice {
  display: flex;
  flex-direction: column;
  margin-bottom: 24px;
}
@media (min-width: 768px) {
  .choice {
    margin-bottom: 40px;
  }
}

.choice__suptitle {
  display: inline-block;
  margin-bottom: 22px;
  font-weight: 600;
}

.choice__item {
  position: relative;
  margin-bottom: 23px;
  margin-left: 34px;
  cursor: pointer;
}
.choice__item .choice__item-select::before {
  position: absolute;
  content: "";
  width: 24px;
  height: 24px;
  border: 2px solid #bdbdbd;
  border-radius: 50%;
  background-color: #ffffff;
  top: 50%;
  left: -20px;
  transform: translate(-50%, -50%);
}
.choice__item input[type=radio]:checked + .choice__item-select::after {
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #7859cf;
  top: 50%;
  left: -20px;
  transform: translate(-50%, -50%);
}
.choice__item input[type=radio]:hover + .choice__item-select::before {
  border-color: #7859cf;
  background-color: #f0ebff;
}
