.filter__item .filter__item-select::before {
  content: "";
  position: absolute;
  width: 42px;
  height: 24px;
  background-color: #bdbdbd;
  border-radius: 12px;
  top: 50%;
  left: -30px;
  transform: translate(-50%, -50%);
